<template>
  <li class="song-list">
    <div class="listletf">
      <p class="text">{{ songList.name }}</p>
      <div class="artists">
        <span v-if="songList.song.sqMusic"></span>
        <p>{{ songList.song.artists[0].name }}-{{ songList.song.album.name }}</p>
      </div>
    </div>
    <div class="listright" @click="clickmusic"><img v-if="songList.id==song?.id" src="../assets/play.gif" alt=""><a href="Javascript:;" v-if="songList.id!=song?.id"></a></div>
  </li>
</template>

<script>
export default {
  props: {
    songList: Object,
    song:Object
  },
  methods: {
    clickmusic(){
      this.$emit("updata-song",this.songList);
    },
  },
};
</script>

<style lang="less" scoped>
.song-list {
  height: 55.5rem;
  display: flex;
  justify-self: start;
  height: 60rem;
  padding: 10px;
  border-bottom: 1px solid #f8f8f9;
  .listletf {
    width: 85%;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    .text {
      font-size: 15rem;
      text-align: left;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      width: 90%;
      height: 30rem;
      display: block;
    }
    .artists {
      margin-top: 3px;
      display: flex;
      justify-self: start;
      align-items: center;
      p {
        color: #ccc;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      span {
        display: block;
        width: 16rem;
        height: 16rem;
        background-image: url("../assets/image-icon.png");
        background-position: 0 3rem;
        background-size: 200rem;
        margin-right: 3rem;
      }
    }
  }
  .listright {
    width: 15%;
    display: flex;
    align-items: center;
    justify-content: center;
    img{
      width: 24rem;
      margin-right: 10rem;
    }
    a {
      color: #aaaaaa;
      width: 100%;
      height: 100%;
      background-image: url("../assets/image-icon.png");
      background-size: 180rem;
      background-repeat: no-repeat;
      background-position: -15px 5px;
    }
    i{
      width: 100%;
      height: 100%;
      display: flex;
      font-size: 30px;
      justify-content: center;
      &.fa-pause-circle-o::before{
        margin: 5rem 6rem 0 0;
      }
    }
  }
}
</style>