<template>
  <div id="app" ref="app">
    <audio
      ref="audio"
      :src="`https://music.163.com/song/media/outer/url?id=${song?.id}.mp3`"
      autoplay
    ></audio>
    <header v-if="$route.meta.shownav" class="header">
      <div class="logo">
        <img src="./assets/logokw.png" alt="">
      </div>
      <a href="Javascript">下载APP</a>
    </header>
    <!-- <router-view @updata-song="song = $event" /> -->
    <router-view @updata-song="addMusic" :song="song" />
    <!-- v-if="song" -->
    <div class="music" v-if="song">
      <canvas ref="canvas" class="canvas" width="75rem" height="70rem"></canvas>
      <div class="img" @click="bigmark = true">
        <img
          v-if="song.picUrl"
          :src="`${song.picUrl}?imageView=1&type=webp&thumbnail=55x0`"
          alt=""
        />
        <img
          v-if="!song.picUrl"
          :src="`${song.al.picUrl}?imageView=1&type=webp&thumbnail=55x0`"
          alt=""
        />
      </div>
      <div class="playing">
        <div class="play-O">
          <span
            ><i class="fa fa-step-backward" @click.stop="playprev"></i
          ></span>
          <span @click.stop="playing"
            ><i ref="play" class="fa fa-pause-circle-o"></i
          ></span>
          <!-- fa-pause-circle-o -->
          <span><i class="fa fa-step-forward" @click.stop="playnext"></i></span>
        </div>
        <div class="play-L" ref="playL">
          <input
            type="range"
            name="play-c"
            id="play-c"
            class="play-c"
            value="0"
            step="1"
            ref="input"
            @input.stop="playmove"
          />
          <div class="play-move" ref="playMove"></div>
          <!-- @mousemove="playmoves($event)" -->
        </div>
        <div v-if="currentTime" class="play-time">
          {{ currentTime }} / {{ duration }}
        </div>
      </div>
      <div class="playList" @click.stop="markup">
        <i class="fa fa-align-justify"></i>
      </div>
    </div>
    <div v-if="!markALL" class="mark" ref="mark">
        <div class="marking"><h1>当前播放</h1>({{playmusic.length}})</div>
        <ul>
          <PlayMusic
            v-for="musicList in playmusic"
            :key="musicList?.id"
            :musicList="musicList"
            @deletemusic="deletemusic"
            @playingmusic="playingmusic"
          />
        </ul>
        <!-- :artist="musicList.song.artists" -->
      </div>
    <!-- :audiocurrentTime="this.$refs.audio.currentTime"
    :audioduration="this.$refs.audio.duration" -->
    <PlayBox
      v-if="bigmark"
      @bigPlaying="bigPlaying"
      :currentTime="currentTime"
      :duration="duration"
      :song="song"
      :playprev="playprev"
      :playnext="playnext"
      :playing="playing"
      :progress="progress"
      :audio="this.$refs.audio"
      :isEdits="isEdit"
      :currentlyrics="currentlyric"
      :parseds="parsed"
      :pattenlist="pattenlist"
      :pattern="pattern"
      @playPattern="playPattern"
      :markup="markup"
      @voice="setVoice"
    />
    <!-- v-if="bigmark" -->
  </div>
</template>

<script>
import PlayMusic from "@/components/playmusic/PlaysMusics.vue";
import PlayBox from "@/components/playBox/PlayBox.vue";
export default {
  data() {
    return {
      song: null,
      isEdit: true,
      markALL: true,
      duration: "",
      currentTime: "",
      playmusic: JSON.parse(localStorage.getItem("playmusic")) || [],
      num: 0,
      bigmark: false,
      parsed: null,
      currentlyric: null,
      canvastime: null,
      pattern: ["顺", "随", "单"],
      pattenlist: "顺",
    };
  },
  components: {
    PlayMusic,
    PlayBox,
  },
  // created(){
  //   this.axios
  //         .get("https://apis.netstart.cn/music//lyric", {
  //           params: {
  //             id: this.song?.id,
  //           },
  //         })
  //         .then((res) => {
  //           let arr = res?.data?.lrc?.lyric.split("\n");
  //           let parsed = arr
  //             .filter((o) => o)
  //             .map((o) => {
  //               let { groups } = o.match(/\[(?<mm>\d{2}):(?<ss>\d{2}\.\d{2,3})\](?<text>.*)/);
  //               return {
  //                 time: groups.mm * 60 + groups.ss * 1,
  //                 text: groups.text.trim(),
  //               };
  //             });
  //           this.parsed = parsed;
  //         });
  // },
  mounted() {
    this.$refs.audio.oncanplay = () => {
      const audio = this.$refs.audio;
      const playMove = this.$refs.playMove;
      const playL = this.$refs.playL;
      const input = this.$refs.input;
      let m =
        parseInt(audio.duration / 60) < 10
          ? "0" + parseInt(audio.duration / 60)
          : parseInt(audio.duration / 60);
      let s =
        parseInt(audio.duration % 60) < 10
          ? "0" + parseInt(audio.duration % 60)
          : parseInt(audio.duration % 60);
      this.duration = m + ":" + s;
      audio.ontimeupdate = () => {
        let timeM =
          parseInt(audio.currentTime / 60) < 10
            ? "0" + parseInt(audio.currentTime / 60)
            : parseInt(audio.currentTime / 60);
        let timeS =
          parseInt(audio.currentTime % 60) < 10
            ? "0" + parseInt(audio.currentTime % 60)
            : parseInt(audio.currentTime % 60);
        this.currentTime = timeM + ":" + timeS;
        let progress = (audio.currentTime / audio.duration) * playL.offsetWidth;
        this.progress = progress;
        playMove.style.width = progress + "rem";
        this.currentlyric = this.findCurrentLyric();
        this.canvastime =
          this.$refs.audio.currentTime / this.$refs.audio.duration;
        audio.onended = () => {
          this.playnext();
        };
      };
    };
    window.addEventListener('scroll',this.handleScroll)
  },
  methods: {
    // 设置音量
    setVoice(value){
      this.$refs.audio.volume=value
    },
    playing() {
      if (this.isEdit) {
        this.$refs.play.className = "fa fa-play-circle-o";
        this.$refs.audio.pause();
        this.isEdit = false;
      } else {
        this.$refs.play.className = "fa fa-pause-circle-o";
        this.$refs.audio.play();
        this.isEdit = true;
      }
    },
    playmove(e) {
      const audio = this.$refs.audio;
      const playMove = this.$refs.playMove;
      const input = this.$refs.input;
      audio.currentTime = (this.$refs.input.value / 100) * audio.duration;
      playMove.style.width = (this.$refs.input.value / 100) * audio.duration;
    },
    addMusic(song) {
      if (!this.isEdit) {
        this.$refs.play.className = "fa fa-pause-circle-o";
        this.isEdit = true;
      }
      if (!this.playmusic.some((o) => o.id == song.id)) {
        this.playmusic.unshift({ ...song, isEdit: true });
        this.playmusic.forEach((item, i) => {
          item.isEdit = false;
        });
        this.playmusic[0].isEdit = true;
      } else {
        this.playmusic = this.playmusic.filter((o) => {
          return o.id != song.id;
        });
        this.playmusic.unshift({ ...song, isEdit: true });
        this.playmusic.forEach((item, i) => {
          item.isEdit = false;
        });
        this.playmusic[0].isEdit = true;
      }
      this.num = 0;
      this.song = song;
    },
    markup() {
      if (this.markALL) {
        this.markALL = false;
        setTimeout(()=>{
          this.$refs.mark.style.top=document.documentElement.scrollTop+220+"rem"
        },10);
      } else {
        this.markALL = true;
      }
    },
    // 删除
    deletemusic(id) {
      this.playmusic = this.playmusic.filter((o) => {
        return o.id != id;
      });
    },
    //点击播放音乐
    playingmusic(music) {
      this.playmusic.forEach((item, i) => {
        item.isEdit = false;
      });
      this.$refs.play.className = "fa fa-pause-circle-o";
      this.isEdit = true;
      music.isEdit = true;
      this.song = music;
      this.num = this.playmusic.indexOf(music);
    },
    //上一首
    playprev() {
      switch (this.pattenlist) {
        case "顺":
          this.num--;
          if (this.num < 0) {
            this.num = this.playmusic.length - 1;
          }
          this.$refs.play.className = "fa fa-pause-circle-o";
          this.isEdit = true;
          break;
        case "随":
          let leng = this.playmusic.filter((o) => o.id != this.song.id);
          this.num = Math.floor(Math.random() * leng.length);
          this.$refs.play.className = "fa fa-pause-circle-o";
          this.isEdit = true;
          break;
        default:
          this.$refs.audio.currentTime=0
          this.$refs.audio.play()
          this.$refs.play.className = "fa fa-pause-circle-o";
          this.isEdit = true;
          break;
      }
    },
    // 下一首
    playnext() {
      switch (this.pattenlist) {
        case "顺":
          this.num++;
          if (this.num > this.playmusic.length - 1) {
            this.num = 0;
          }
          this.$refs.play.className = "fa fa-pause-circle-o";
          this.isEdit = true;
          break;
        case "随":
          let leng = this.playmusic.filter((o) => o.id != this.song.id);
          this.num = Math.floor(Math.random() * leng.length);
          this.$refs.play.className = "fa fa-pause-circle-o";
          this.isEdit = true;
          break;
        default:
          this.$refs.audio.currentTime=0
          this.$refs.audio.play()
          this.$refs.play.className = "fa fa-pause-circle-o";
          this.isEdit = true;
          break;
      }
    },
    // 大个的播放器开关
    bigPlaying(falses) {
      this.bigmark = falses;
    },
    findCurrentLyric() {
      var copy = [...this.parsed].reverse();
      var index = copy.findIndex((o) => o.time < this.$refs.audio.currentTime);
      return {
        index: copy.length - 1 - index,
        text: copy[index]?.text,
      };
    },
    // 播放模式
    playPattern(num) {
      this.pattenlist = this.pattern[num];
    },
    handleScroll(){
      if(!this.markALL){
        this.$refs.mark.style.top=document.documentElement.scrollTop+220+"rem"
      }
    }
  },
  watch: {
    song(news) {
      if (news) {
        this.$refs.app.style.paddingBottom = "70rem";
        this.axios
          .get("https://apis.netstart.cn/music/lyric", {
            params: {
              id: news?.id,
            },
          })
          .then((res) => {
            let arr = res?.data?.lrc?.lyric?.split("\n");
            let parsed = arr
              .filter((o) => o)
              .map((o) => {
                let { groups } = o?.match(
                  /\[(?<mm>\d{2}):(?<ss>\d{2}\.\d{2,3})\](?<text>.*)/
                );
                return {
                  time: groups.mm * 60 + groups.ss * 1,
                  text: groups.text.trim(),
                };
              });
            this.parsed = parsed;
          });
      }
    },
    playmusic: {
      deep: true,
      handler(newmusic) {
        // console.log(1);
        localStorage.setItem("playmusic", JSON.stringify(newmusic));
      },
    },
    num: {
      deep: true,
      handler(news) {
        this.num = news;
        this.playmusic.forEach((item, i) => {
          item.isEdit = false;
        });
        this.playmusic[news].isEdit = true;
        this.song = this.playmusic[news];
      },
    },
    canvastime(news) {
      const canvas = this.$refs.canvas;
      const context = canvas.getContext("2d");
      context.clearRect(0, 0, 75, 70);
      context.beginPath();
      context.arc(37.5, 35, 30, -0.5 * Math.PI, Math.PI * (2 * news - 0.5));
      context.strokeStyle = "#ffffff";
      context.lineWidth = "3";
      context.stroke();
    },
  },
  beforeUpdate() {},
};
</script>

