<template>
<li class="person-list" @click="$emit('JumpDetail',personList.id)">
  <!-- new String(personList.playCount).length>8 ? new String(personList.playCount).slice(0,1)+'亿': String(personList.playCount).slice(0,4)+'万' -->
  <span class="icon"><img src="../assets/play-kw.png" alt=""><p>{{playVolume}}</p></span>
  <img :src="`${personList.picUrl}?imageView=1&type=webp&thumbnail=120x0`" alt=""/>
  <p>{{personList.name}}</p>
</li>
</template>

<script>
export default {
  name: 'personList',
  props: {
    personList:Object,
  },
  computed:{
    playVolume(){
      return this.personList.playCount >100000000 ? `${(this.personList.playCount /100000000).toFixed(1)}亿`: this.personList.playCount >10000 ? `${(this.personList.playCount /10000).toFixed(1)}万`: this.personList.playCount;
    }
  }
}
</script>

