<template>
  <div>
    <div v-if="!markbg" class="markbg">
    <img src="../assets/play.gif" alt="" />
  </div>
  <div v-if="markbg">
    <div class="Search" @click="goSearch">
      <div class="SearchView">
        <span><img src="../assets/search.svg" alt="" /></span>
        <p>搜索你想听的音乐</p>
      </div>
    </div>
    <h3>酷我排行榜</h3>
    <ul class="song">
      <li class="song-g" @click="goAboutView">
        <div class="icon"><img src="../assets/icon_rank_hot.png" alt=""></div>
        <div class="cg">
          <span>酷我热歌榜<i class="fa fa-angle-right"></i></span>
          <div class="songkw">
            <li v-for="(hot,index) in hotsong" :key="hot.id">{{index+1}}.{{hot.ar[0]?.name}} - {{hot.name}}</li>
          </div>
        </div>
      </li>
      <li class="song-g" @click="gosoar">
        <div class="icon"><img src="../assets/icon_rank_rise.png" alt=""></div>
        <div class="cg">
          <span>酷我飙升榜<i class="fa fa-angle-right"></i></span>
          <div class="songkw">
            <li v-for="(soar,index) in soars.tracks" :key="index">{{index+1}}.{{soar.second}} - {{soar.first}}</li>
          </div>
        </div>
      </li>
      <li class="song-g" @click="gonewsong">
        <div class="icon"><img src="../assets/icon_rank_new.png" alt=""></div>
        <div class="cg">
          <span>酷我新歌榜<i class="fa fa-angle-right"></i></span>
          <div class="songkw">
            <li v-for="(news,index) in newsong.tracks" :key="news.id">{{index+1}}.{{news.second}} - {{news.first}}</li>
          </div>
        </div>
      </li>
    </ul>
    <!-- <ul class="song">
    <SongList v-for="songList in newsong" :key="songList.id" :songList="songList" @updata-song="$emit('updata-song',$event)" :song="song"/>
    </ul> -->
    <h3>推荐歌单</h3>
    <ul class="home">
    <PersonList @JumpDetail="JumpDetail" v-for="personList in personalized" :key="personList.id" :personList="personList"/>
    </ul>
  </div>
  </div>
</template>

<script>
// @ is an alias to /src
import PersonList from '@/components/PersonList.vue'
import SongList from '@/components/SongList.vue'

export default {
  name: 'HomeView',
  components: {
    PersonList,
    SongList
  },
  props:['song'],
  data(){
    return{
      personalized:[],
      newsong:[],
      hotsong:[],
      soars:[],
      markbg:false
    }
  },
  created(){
    Promise.all([
      this.axios.get("https://apis.netstart.cn/music/personalized",{
        params:{limit:6},
      }),
      this.axios.get("https://apis.netstart.cn/music/playlist/detail", {
        params: { id: 3778678 },
      }),
      this.axios.get("https://apis.netstart.cn/music/toplist/detail"),
    ]).then(([p,m,s]) => {
      this.personalized=p.data.result
      this.newsong=s.data.list[1];
      this.hotsong = m.data.playlist.tracks.slice(0, 3);
      this.soars=s.data.list[0];
      this.markbg=true
    }).catch((err) => {
      console.log(err);
    });
  },
  methods:{
    JumpDetail(id){
      this.$router.push({path:'/detail',query:{id:id}});
    },
    goSearch(){
      this.$router.push('/search')
    },
    goAboutView(){
      this.$router.push('/about')
    },
    gonewsong(){
      this.$router.push('/newsong')
    },
    gosoar(){
      this.$router.push('/soar')
    }
  }
}
</script>

