        <p v-if="musicList.ar">{{musicList.ar[0].name}}</p>
        <p v-else-if="musicList.song.artists[0]">{{musicList.song.artists[0].name}}</p>
        <span @click.stop="$emit('deletemusic',musicList.id)"><img src="../../assets/close-o.svg" alt=""></span>
    </div>
   </li>
