<template>
  <!-- @click="$emit('bigmark',bigmark=false)" -->
  <div class="playBox">
    <div class="bg">
      <img
        v-if="song.picUrl"
        :src="
          song ? `${song.picUrl}?imageView=1&type=webp&thumbnail=375x0` : ''
        "
        alt=""
      />
      <img
        v-if="!song.picUrl"
        :src="
          song ? `${song.al.picUrl}?imageView=1&type=webp&thumbnail=375x0` : ''
        "
        alt=""
      />
    </div>
    <div class="top">
      <i class="fa fa-angle-down" @click="$emit('bigPlaying', false)"></i>
      <transition name="fade1">
        <span class="acc" v-if="num==0">列表模式</span>
      </transition>
      <transition name="fade2">
        <span class="acc" v-if="num==1">随机模式</span>
      </transition>
      <transition name="fade3">
        <span class="acc" v-if="num==2">单曲模式</span>
      </transition>
      <div class="singer">
        <p>{{ song.name }}</p>
        <span v-if="song.ar">{{ song.ar[0].name }}</span>
        <span v-else-if="song.song.artists">{{
          song.song.artists[0].name
        }}</span>
      </div>
      <div class="share">
        <img
          v-if="song.picUrl"
          :src="
            song ? `${song.picUrl}?imageView=1&type=webp&thumbnail=30x0` : ''
          "
          alt=""
        />
        <img
          v-if="!song.picUrl"
          :src="
            song ? `${song.al.picUrl}?imageView=1&type=webp&thumbnail=30x0` : ''
          "
          alt=""
        />
        <span><i class="fa fa-share-alt"></i></span>
      </div>
    </div>
    <div v-if="lyricup" class="center">
      <div class="center-top">
        <span ref="playg"><img src="../../assets/play-g-.png" alt="" /></span>
      </div>
      <div class="centerbig" @click="lyricups">
        <div class="centerg" ref="centerg">
          <div class="centersrall">
            <img
              v-if="song.picUrl"
              :src="
                song
                  ? `${song.picUrl}?imageView=1&type=webp&thumbnail=200x0`
                  : ''
              "
              alt=""
            />
            <img
              v-if="!song.picUrl"
              :src="
                song
                  ? `${song.al.picUrl}?imageView=1&type=webp&thumbnail=200x0`
                  : ''
              "
              alt=""
            />
          </div>
        </div>
      </div>
    </div>
    <div class="footer" ref="footer">
      <div class="lyric" ref="lyric">
        <ul ref="parse">
          <li
            ref="list"
            v-for="(parse, index) in parsed"
            :key="index"
            :class="index == currentlyric.index ? 'active' : ''"
          >
            {{ parse.text }}
          </li>
          <!-- :class="index==currentlyric.index ? 'active' : ''" -->
        </ul>
      </div>
      <div class="playing" @click="lyricdowns">
        <div class="playing-top">
            <span>{{ currentTime }}</span>
        </div>
        <div class="playing-center">
          <div class="play-o">
            <span><i class="fa fa-step-backward" @click="playprevs"></i></span>
            <span @click="playingbig"
              ><i ref="play" class="fa fa-pause-circle-o"></i
            ></span>
            <span><i class="fa fa-step-forward" @click="playnexts"></i></span>
          </div>
          <div class="play-l" ref="playL">
            <input
              type="range"
              value="0"
              step="1"
              ref="input"
              @input="playmove"
            />
            <!-- @input="playmove" -->
            <div class="mark" ref="playMove"><span></span></div>
          </div>
          <div class="play-time" v-if="currentTime">
            <!-- fa-random  -->
            <!-- fa-heart-o -->
            <!-- fa-heart -->
            <!-- fa-refresh -->
            <!-- fa-repeat -->
            <span @click="playPatternlist">
              <i ref="Pattern" class="fa fa-refresh"></i>
            </span>
            <span><i class="fa fa-heart-o"></i></span>
            <span @click="markup"><img src="../../assets/list.png" alt=""></span>
            <span @click="vode">
              <img src="../../assets/voice.png" alt="">
              <div class="voice" ref="voice">
                <div class="mark" ref="mark2"></div>
                <input ref="input2" type="range" name="voice" id="voice" value="100" step="1" @input="voice">
              </div>
              </span>
          </div>
        </div>
        <div class="playing-footer">
            <span>{{ duration }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  // 'currentTime isEdit:true parsed:null,currentlyric:null,'
  props: [
    "duration",
    "currentTime",
    "song",
    "playprev",
    "playnext",
    "playing",
    "audio",
    "isEdits",
    "parseds",
    "currentlyrics",
    "pattern",
    "pattenlist",
    "markup"
  ],
  data() {
    return {
      isEdit: true,
      parsed: null,
      currentlyric: null,
      num:0,
      arr:[],
      add:0,
      lyricup:true,
      value:0,
      voiceup:true
    };
  },
  mounted() {
    const playL = this.$refs.playL;
    const playMove = this.$refs.playMove;
    let progress =
      (this.audio.currentTime / this.audio.duration) * playL.offsetWidth;
    playMove.style.width = progress + "rem";
    this.isEdit = this.isEdits;
    this.audio.onended = () => {
      this.$refs.lyric.style.opacity = 0;
    };
    // console.log(this.isEdits);
    if (!this.isEdits) {
      this.$refs.play.className = "fa fa-play-circle-o";
      this.$refs.centerg.style.animationPlayState = "paused";
      this.$refs.playg.style.transform = "rotate(-40deg)";
    } else {
      this.$refs.play.className = "fa fa-pause-circle-o";
      this.$refs.centerg.style.animationPlayState = "running";
      this.$refs.playg.style.transform = "rotate(-5deg)";
    }
    if(this.pattenlist=="顺"){
        this.$refs.Pattern.className="fa fa-refresh"
        this.num=0
      }else if(this.pattenlist=="随"){
        this.$refs.Pattern.className="fa fa-random"
        this.num=1
      }else if(this.pattenlist=="单"){
        this.$refs.Pattern.className="fa fa-repeat"
        this.num=2
      }
  },
  methods: {
    playingbig() {
      if (this.isEdits) {
        this.$refs.play.className = "fa fa-play-circle-o";
        this.$refs.centerg.style.animationPlayState = "paused";
        this.$refs.playg.style.transform = "rotate(-40deg)";
        this.isEdit = false;
        this.playing();
      } else {
        this.$refs.play.className = "fa fa-pause-circle-o";
        this.$refs.centerg.style.animationPlayState = "running";
        this.$refs.playg.style.transform = "rotate(-5deg)";
        this.isEdit = true;
        this.playing();
      }
    },
    playmove() {
      const playMove = this.$refs.playMove;
      const input = this.$refs.input;
      this.audio.currentTime = (input.value / 100) * this.audio.duration;
      playMove.style.width = (input.value / 100) * this.audio.duration;
    },
    playprevs() {
      if (!this.isEdit) {
        this.$refs.play.className = "fa fa-pause-circle-o";
        this.$refs.centerg.style.animationPlayState = "running";
        this.$refs.playg.style.transform = "rotate(-5deg)";
      }
      if(this.pattenlist !=="单"){
        this.$refs.lyric.style.opacity = 0;
      }
      this.playprev();
    },
    playnexts() {
      if (!this.isEdit) {
        this.$refs.play.className = "fa fa-pause-circle-o";
        this.$refs.centerg.style.animationPlayState = "running";
        this.$refs.playg.style.transform = "rotate(-5deg)";
      }
      if(this.pattenlist !=="单"){
        this.$refs.lyric.style.opacity = 0;
      }
      this.playnext();
    },
    // 播放模式
    playPatternlist(){
      this.num++
      if(this.num>this.pattern.length-1){
        this.num=0
      }
      if(this.num==0){
        this.$refs.Pattern.className="fa fa-repeat"
        // this.$refs.mod.opacity=1;
        // setTimeout(()=>{
        //   this.$refs.mod.opacity=0;
        // },2000)
      }else if(this.num==1){
        this.$refs.Pattern.className="fa fa-random"
        // this.$refs.mod[1].opacity=1;
        // setTimeout(()=>{
        //   this.$refs.mod[1].opacity=0;
        // },2000)
      }else if(this.num==2){
        this.$refs.Pattern.className="fa fa-refresh"
        // this.$refs.mod[2].opacity=1;
        // setTimeout(()=>{
        //   this.$refs.mod[2].opacity=0;
        // },2000)
      }
      this.$emit('playPattern',this.num);
    },
    lyricups(){
      this.lyricup=false
      this.$refs.lyric.style.height=470+"rem"
      this.$refs.footer.style.height=620+"rem"

    },
    lyricdowns(){
      this.lyricup=true
      this.$refs.lyric.style.height=150+"rem"
      this.$refs.footer.style.height=300+"rem"
    },
    voice(){
      this.$refs.mark2.style.width =this.$refs.input2.value / 1.25+"rem";
      this.$emit('voice',this.$refs.input2.value/100);
    },
    vode(){
      if(this.voiceup){
        this.$refs.voice.style.width=80+"rem";
        this.voiceup=false
      }else{
        this.$refs.voice.style.width=0+"rem";
        this.voiceup=true
      }
    }
  },
  watch: {
    isEdits(news) {
      this.isEdit = news;
    },
    parsed(news) {
      if (news) {
        this.$refs.lyric.style.opacity = 1;
        this.arr=[]
        this.$refs.list?.forEach((o,i)=>{
        this.arr.push(o.offsetHeight)
      });
      }
    },
    currentlyric(news) {
      if (news) {
        this.add = this.arr?.slice(0,news.index).reduce((a,b)=>{
         return a+b
        });
        this.$refs.parse.style.marginTop = -this.add+this.$refs.lyric.offsetHeight / 2+ "rem"
        // this.$refs.parse.style.marginTop =
        //   -news.index * (this.$refs.lyric.offsetHeight / 5) +
        //   this.$refs.lyric.offsetHeight / 2 +
        //   "rem";
      }
    },
  },
  beforeUpdate() {
    const playL = this.$refs.playL;
    const playMove = this.$refs.playMove;
    let progress =
      (this.audio.currentTime / this.audio.duration) * playL.offsetWidth;
    playMove.style.width = progress + "rem";
    this.currentlyric = this.currentlyrics;
    this.parsed = this.parseds;
  },
};
</script>

<style lang="less" scoped>
.fade1-enter-active,.fade2-enter-active,.fade3-enter-active {
  transition: all 1s ease;
}

.fade1-enter,.fade1-leave,.fade2-enter,.fade2-leave,.fade3-enter,.fade3-leave{
  opacity: 0;
}

.fade1-enter-to,.fade1-leave-to,.fade2-enter-to,.fade2-leave-to,.fade3-enter-to,.fade3-leave-to{
  opacity: 1;
}
.playBox {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1200;
  background-color: rgba(0, 0, 0, 1);
  display: flex;
  flex-direction: column;
  .bg {
    width: 100vw;
    height: 100vh;
    background-color: black;
    position: absolute;
    z-index: 1300;
    display: flex;
    align-items: center;
    filter: blur(10rem) brightness(75%);
    transform: scale(1.4);
    &::before {
      content: " ";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      // box-shadow: 0 0 30rem 60rem #fff inset;
    }
    img {
      width: 100vw;
      height: 100vh;
    }
  }
  .top {
    width: 100%;
    height: 45rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    z-index: 1400;
    padding: 0 5rem;
    i {
      width: 45rem;
      height: 45rem;
      // background-color: rgba(255, 255, 255, 0.3);
      font-size: 30rem;
      color: #f7f7f7;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
    }
    .acc{
        position: absolute;
        top: 50%;
        left: 15%;
        transform: translateY(-50%);
        width: 80rem;
        height: 25rem;
        font-size: 14rem;
        background-color: #f7f7f7;
        color: #878787;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 15rem;
        // transition: all 0.5s ease-out;
        // opacity: 0;
      }
    .singer {
      width: 25%;
      margin-left: 30rem;
      overflow: hidden;
      p {
        font-size: 16rem;
        font-weight: 540;
        color: #fff;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      span {
        color: #fff;
      }
    }
    .share {
      display: flex;
      justify-self: start;
      align-items: center;
      img {
        border-radius: 50%;
      }
      span {
        width: 40rem;
        i {
          font-size: 20rem;
        }
      }
    }
  }
  .center {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: relative;
    z-index: 1400;
    .center-top {
      width: 100%;
      height: 80rem;
      display: flex;
      justify-content: center;
      align-items: center;
      span {
        width: 60rem;
        height: 100%;
        position: relative;
        top: 50%;
        left: 8%;
        z-index: 999;
        transform-origin: top left;
        transform: rotate(-5deg);
        transition: all 0.5s linear;
        img {
          width: 60rem;
          position: absolute;
          top: -7rem;
          left: -10rem;
        }
      }
    }
    .centerbig {
      width: 240rem;
      height: 240rem;
      background-color: #363636;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      .centerg {
        width: 220rem;
        height: 220rem;
        background-color: #1b1c1e;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        box-shadow: 0 0 15rem 0 black inset;
        animation: playing 12s linear infinite;
        .centersrall {
          width: 180rem;
          height: 180rem;
          border-radius: 50%;
          background-color: black;
          overflow: hidden;
          border: 10rem solid black;
        }
      }
    }
  }
  .footer {
    width: 100%;
    height: 300rem;
    position: relative;
    z-index: 1400;
    .lyric {
      width: 100%;
      height: 150rem;
      overflow: hidden;
      opacity: 1;
      display: flex;
      justify-content: center;
      position: relative;
      ul {
        width: 70%;
        height: 100%;
        transition: all 1s linear;
        li {
          display: block;
          color: #fff;
          font-size: 14rem;
          padding:5rem 0;
          &.active {
            transition: all 0.5s linear;
            color: red;
            transform: scale(1.2);
          }
        }
      }
    }
    .playing {
      height: 150rem;
      display: flex;
      justify-content: start;
      align-items: center;
      .playing-top{
        width: 15%;
        height: 100%;
        span{
            width: 100%;
            display: block;
            color: #fff;
            margin-top: 92rem;
            text-align: right;
        }
      }
      .playing-center {
        height: 100%;
        width: 70%;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        flex-direction: column;
        .play-o {
          display: flex;
          justify-content: center;
          align-items: center;
          span {
            font-size: 25rem;
            color: #fff;
          }
          span:nth-of-type(2) {
            margin: 0 35rem;
            font-size: 70rem;
          }
        }
        .play-l {
          width: 250rem;
          height: 6rem;
          border: 1rem solid #878787;
          background-color: #4d4d4f;
          border-radius: 4rem;
          // overflow: hidden;
          position: relative;
          input {
            width: 250rem;
            height: 6rem;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1000;
            opacity: 0;
          }
          .mark {
            width: 0rem;
            height: 6rem;
            background-color: #ec4141;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 999;
            span {
              position: absolute;
              top: -5rem;
              right: 0;
              display: block;
              transform: translate(50%);
              width: 15rem;
              height: 15rem;
              background-color: #ec4141;
              border-radius: 50%;
            }
          }
        }
        .play-time {
          width: 100%;
          height: 35rem;
          font-size: 20rem;
          color: #fff;
          display: flex;
          justify-content: space-evenly;
          align-items: center;
          span{
            width: 20rem;
            height: 20rem;
            position: relative;
            .voice{
              position: absolute;
              width: 0rem;
              border-radius: 4rem;
              height: 8rem;
              border: #ec4141 solid 1px;
              overflow: hidden;
              top: 50%;
              left: 25rem;
              transform: translateY(-50%);
              transition: all 0.5s linear;
              input{
                width: 100%;
                height: 6rem;
                position: absolute;
                top: 0;
                left: 0;
                z-index: 999;
                opacity: 0;
              }
              .mark{
                width: 100%;
                height: 6rem;
                position: absolute;
                top: 0;
                left: 0;
                background-color: #ec4141;
              }
            }
          }
        }
      }
      .playing-footer{
        width: 15%;
        height: 100%;
        span{
            width: 100%;
            display: block;
            color: #fff;
            margin-top: 92rem;
            text-align: left;
            position: relative;
        }
      }
    }
  }
}

@keyframes playing {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
</style>